<template>
  <div id="editOneWrapper">
    <div id="editOne">
      <p class="cancel" @click="cancelClick"></p>
      <div class="flexLayout">
        <p class="label">
          <label for="categoryName">类别名称：</label>
        </p>
        <p>
          <input
            type="text"
            id="categoryName"
            v-model="categoryName"
            ref="autoFocusInput"
          />
          <!-- <div>{{editIndex.name}}</div> -->
        </p>
      </div>
      <div class="flexLayout" style="margin-top: 30px">
        <p class="label">
          <label
            >上传类别大图：<input
              type="file"
              id="bigImageFileInput"
              @change="onFileChange"
          /></label>
        </p>
        <div class="imageP" @click="bigImageClick">
          <img
            v-lazy="editIndex.image"
            alt="big image of category"
            title="单击修改大图"
            id="bigImage"
          />
        </div>
      </div>
      <div class="flexLayout" style="margin-top: 30px">
        <p class="label">
          <label
            >上传类别小图：<input
              type="file"
              id="thumbFileInput"
              @change="onFileChange"
          /></label>
        </p>
        <div class="thumbP" @click="thumbClick">
          <!-- <img src="../../../../../assets/image/providerTools/categoryManagement/add.svg" alt="" id="thumb"> -->
          <img
            v-lazy="editIndex.thumbnail"
            alt="thumb image of category"
            title="单击修改小图"
            id="thumb"
          />
        </div>
      </div>
      <p class="buttonZone">
        <button class="cancel other-button" @click="cancelClick">取消</button>
        <button class="ensure other-button" @click="ensureClick">确认</button>
      </p>
      <!-- <p>{{ editIndex }}</p> -->
    </div>
  </div>
</template>

<script>
import { $uploadStreamMediaFile } from "../../../../../network/common/common";
import { UrlDecode } from "../../../../../common/utils";
export default {
  name: "e-editOne",
  data() {
    return {
      categoryName: this.editIndex.name,
      bigImageImage: this.editIndex.image,
      thumbImage: this.editIndex.thumbnail,
      tempBigImageValue: null,
      tempThumbValue: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.autoFocusInput.focus();
    });
  },
  props: {
    editNum: {
      type: Number,
      default() {
        return 0;
      },
    },
    editIndex: {
      type: Array,
      default() {
        return [];
      },
    },
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  activated() {
    console.log(this.editIndex);
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    cancelClick() {
      this.$emit("cancelClick");
    },
    async onFileChange(e) {
      if (this.categoryName === "") {
        this.$emit("emptyCategoryName");
        return;
      }
      let files = e.target.files || e.dataTransfer.files;
      const inputId = e.target.getAttribute("id");
      // 判断上传文件是否是空文件
      if (!files.length) return;
      // 判断上传文件是否为图片文件
      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      if (!/image\/\w+/.test(files[0].type)) {
        this.$toast.error("文件必须为图片！", 240);
        const fileInput = document.getElementById(inputId);
        fileInput.value = "";
        return;
      }
      //  以上判断均通过后向服务端进行请求
      const formData = new FormData();
      formData.append("image", files[0]);
      const response = await $uploadStreamMediaFile(formData);
      const { details } = response;
      const { image_url } = details;
      if (image_url) {
        if (inputId === "bigImageFileInput") {
          this.bigImageImage = image_url;
          const img = document.querySelector("#bigImage");
          img.setAttribute("src", "");
          img.style.width = "144px";
          img.style.height = "144px";
          img.setAttribute("src", image_url);
        }
        if (inputId === "thumbFileInput") {
          // eslint-disable-next-line camelcase
          this.thumbImage = image_url;
          const img = document.querySelector("#thumb");
          img.setAttribute("src", "");
          img.style.width = "70px";
          img.style.height = "70px";
          img.setAttribute("src", image_url);
        }
        this.$emit("imageUploadSuccess");
      } else {
        alert("图片上传失败");
      }
    },
    bigImageClick() {
      if (this.categoryName === "") {
        this.$emit("emptyCategoryName");
        return;
      }
      //   const findInList = this.list.find(
      //     (item) => item.name === this.categoryName
      //   );
      //   if (findInList) {
      //     this.$emit("hadSameTitleFromList");
      //     return;
      //   }
      const fileInputOfBigImage = document.querySelector("#bigImageFileInput");
      fileInputOfBigImage.click();
    },
    thumbClick() {
      if (this.categoryName === "") {
        this.$emit("emptyCategoryName");
        return;
      }
      //   const findInList = this.list.find(
      //     (item) => item.name === this.categoryName
      //   );
      //   if (findInList) {
      //     this.$emit("hadSameTitleFromList");
      //     return;
      //   }
      const fileInputOfThumb = document.querySelector("#thumbFileInput");
      fileInputOfThumb.click();
    },
    ensureClick() {
      if (!this.categoryName || !this.bigImageImage || !this.thumbImage) {
        alert("请完善信息");
        return;
      }
      const param = {
        id: this.editIndex.id,
        name: this.categoryName,
        date_created: new Date(),
        image: this.bigImageImage,
        thumbnail: this.thumbImage,
      };
      console.log(param);
      this.$emit("ensureClick", param);
    },
  },
};
</script>

