<template>
  <div id="category">
    <div class="buttonZone">
      <p @click="showAddOneDialog" class="addCategoryBtn">添加类别</p>
      <!-- <p class="simulatedLine"></p> -->
      <!-- <p class="submitBtn" @click="showEditReasonDialog">提交审核</p> -->
      <!-- <p class="simulatedLine"></p> -->
      <!-- <p class="giveUpModificationBtn" @click="resetListAndOrder">清除修改</p> -->
    </div>
    <Scroll
      class="scroll-content"
      ref="scroll"
      @scroll="printPosition"
      :probe-type="3"
      v-if="showimg"
    >
      <content-list
        :list="list"
        @deleteCategoryClick="deleteCategoryItem"
        @newImageUpload="newImageUpload"
        @emptyNewTitle="emptyNewTitleToast"
        @findSameTitle="findSameTitleToast"
        @updateTitle="updateTitle"
        @titleNoChange="titleNoChange"
        @editDialogshow="editDialogshow"
      ></content-list>
    </Scroll>
    <transition name="super">
      <div class="right-content-cover-absolute" v-if="isShowAddOneDialog">
        <add-one
          :list="list"
          @ensureClick="addOne"
          @cancelClick="cancelClickOfAddOne"
          @emptyCategoryName="emptyCategoryNameToast"
          @hadSameTitleFromList="hadSameTitleFromListToast"
          @imageUploadSuccess="imageUploadSuccessToast"
          ref="addone"
        ></add-one>
      </div>
    </transition>

    <transition name="super">
      <div class="right-content-cover-absolute" v-if="isShowEditOneDialog">
        <edit-one
          :editIndex="editIndex"
          :editNum="editNum"
          :list="list"
          @cancelClick="cancelClickOfEditOne"
          @emptyCategoryName="emptyCategoryNameToast"
          @ensureClick="editOne"
        ></edit-one>
      </div>
    </transition>

    <transition name="super">
      <div class="right-content-cover-absolute" v-show="isShowEditReasonDialog">
        <edit-reason-dialog
          @ensureClick="deployOrder"
          @cancelClick="cancelClickOfEditReasonDialog"
          ref="editReasonDialog"
        ></edit-reason-dialog>
      </div>
    </transition>

    <div class="absoluteFullLayout" v-show="isShowCoverPage"></div>

    <back-top @click="scrollToTop" v-show="ifShowBackTop"></back-top>

    <loading :isLoading="isLoading"></loading>
  </div>
</template>

<script>
// @ is an alias to /src
import Scroll from "../../../components/common/scroll/Scroll.vue";
import ContentList from "./childComponents/contentList/ContentList.vue";
import { debounce, UrlDecode } from "../../../common/utils";
import AddOne from "./childComponents/addOne/AddOne.vue";
import EditOne from "./childComponents/editOne/EditOne.vue";
import {
  $getCategories,
  $deployOrder,
} from "../../../network/adminTools/category";
import { $uploadStreamMediaFile } from "../../../network/common/common";
// eslint-disable-next-line import/extensions
import EditReasonDialog from "../../../components/content/adminTools/editReasonDialog/EditReasonDialog.vue";
import BackTop from "../../../components/common/backTop/BackTop.vue";
import Loading from "../../../components/common/loading/Loading.vue";

export default {
  name: "e-Category",
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010001", 0);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010001");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
    }
  },
  async beforeRouteLeave(to, from, next) {
    window.stop();
    if (
      this.order.add.length === 0 &&
      this.order.update.length === 0 &&
      this.order.delete.length === 0
    ) {
      //this.$refs.scroll.scrollTo(0, 0, 300);
      next();
    } else {
      if (confirm("您有修改未提交，确认离开？")) {
        this.order = {
          add: [],
          update: [],
          delete: [],
        };
        window.localStorage.setItem("order", window.JSON.stringify(this.order));

        const tempOriginList = window.JSON.parse(
          window.JSON.stringify(this.originList)
        );
        this.list = tempOriginList;
        window.localStorage.setItem("list", window.JSON.stringify(this.list));
        //this.$refs.scroll.scrollTo(0, 0, 300);
        next();
      } else {
        window.localStorage.setItem("currentIndex010001", 0);
        this.$bus.$emit("setCurrentIndex010001To0");
        next(false);
      }
    }
  },
  async mounted() {
    // console.log(this.cookieOperations.getCookie('xxxxxxx'));
    //this.isLoading = true;
    console.log("activitated", this);
    if (this.$refs.scroll) {
      this.$refs.scroll.refresh();
    }

    // 每次都进行请求是为了保存初始数据
    const response = await $getCategories();
    console.log("categories", response);
    const { details } = response;
    const valueArray = Object.values(details);
    // 如果是第一次进入该页面，则浏览器的本地存储中不会保存有list这个项目
    //20220127先修改成每一次都获取类别
    // if (!window.localStorage.getItem("list")) {
    //   // eslint-disable-next-line array-callback-return
    //   valueArray.forEach((item) => {
    //     this.list.push(window.JSON.parse(window.JSON.stringify(item)));
    //     this.originList.push(window.JSON.parse(window.JSON.stringify(item)));
    //   });
    //   window.localStorage.setItem("list", window.JSON.stringify(this.list)); // 赋序列化后的值
    //   window.localStorage.setItem("order", window.JSON.stringify(this.order)); //赋序列化后的值
    // }
    // // 如果不是第一次进入该页面，则浏览器的本地存储中会保存有list这个项目，可以直接使用它，而不用去等请求服务端返回的数据，用户体验会稍好一点（前提是没有手动修改list这个key的值）
    // else {
    //   this.list = window.JSON.parse(window.localStorage.getItem("list")); // 先进行反序列化
    //   this.order = window.JSON.parse(window.localStorage.getItem("order")); // 先进行反序列化

    //   this.originList.splice(0, this.originList.length);
    //   valueArray.forEach((item) => {
    //     this.originList.push(window.JSON.parse(window.JSON.stringify(item)));
    //   });
    // }
    valueArray.forEach((item) => {
      this.list.push(window.JSON.parse(window.JSON.stringify(item)));
      this.originList.push(window.JSON.parse(window.JSON.stringify(item)));
    });
    window.localStorage.setItem("list", window.JSON.stringify(this.list)); // 赋序列化后的值
    window.localStorage.setItem("order", window.JSON.stringify(this.order)); //赋序列化后的值

    if (this.$refs.scroll) {
      this.debounceRefresh = debounce(this.$refs.scroll.refresh, 500);
    }
    this.$bus.$on("itemImageLoad", () => {
      //this.debounceRefresh();
    });
  },
  activated() {
    this.showimg = false;
    let page = this;
    setTimeout(function () {
      page.showimg = true;
    }, 100);
    console.log(233);
  },
  deactivated() {
    this.$bus.$off("itemImageLoad");
  },
  data() {
    return {
      showimg: true,
      isLoading: false,
      debounceRefresh: null,
      isShowAddOneDialog: false,
      isShowEditOneDialog: false,
      list: [],
      order: {
        add: [],
        update: [],
        delete: [],
      },
      isShowCoverPage: false,
      activeEditID: -1,
      isShowEditReasonDialog: false,
      ifShowBackTop: false,

      originList: [],
      editNum: 0,
      editIndex: [],
    };
  },
  methods: {
    // 删除一个类别数据
    deleteCategoryItem(index) {
      const temp = window.JSON.parse(window.JSON.stringify(this.list[index])); // 浅拷贝了一份将要被删除的类别数据
      /* const tempList = window.JSON.parse(window.JSON.stringify(this.list));
      tempList.splice(index, 1);
      this.list = tempList; */
      // this.list.splice(index, 1); //本地不显示已修改
      window.localStorage.setItem("list", window.JSON.stringify(this.list));
      // 去看原始的list中是否存在该id对应的类别对象
      //20220127暂时删去
      const findInOriginList = this.originList.find(
        (item) => item.id === temp.id
      );
      // //const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
      // if (findInOriginList) {
      //   this.order.delete.push(findInOriginList); // 保存最原始的name, image和thumbbail
      //   const findInUpdate = this.order.update.find(
      //     (item) => item.id === temp.id
      //   );
      //   if (findInUpdate) {
      //     const index = this.order.add.indexOf(findInUpdate);
      //     this.order.update.splice(index, 1);
      //   }
      // } else {
      //   const findInAdd = this.order.add.find((item) => item.id === temp.id);
      //   if (findInAdd) {
      //     const index = this.order.add.indexOf(findInAdd);
      //     this.order.add.splice(index, 1);
      //   }
      // }
      this.order.delete.push(findInOriginList);
      console.log(findInOriginList);
      window.localStorage.setItem("order", window.JSON.stringify(this.order));
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    showAddOneDialog() {
      this.isShowCoverPage = true;
      this.isShowAddOneDialog = true;
    },
    // 增加一个类别对象
    addOne(param) {
      this.isShowAddOneDialog = false;
      this.isShowCoverPage = false;
      const { name } = param;
      const findInList = this.list.find((item) => item.name === name);
      if (findInList) {
        this.$toast.warn("该类别已存在", 240);
        return;
      }
      /* const tempList = window.JSON.parse(window.JSON.stringify(this.list));
      tempList.push(param);
      this.list = tempList; */
      // this.list.push(param); //本地不显示修改
      window.localStorage.setItem("list", window.JSON.stringify(this.list));
      this.$refs.scroll.refresh();
      setTimeout(() => {
        this.$refs.scroll.scrollTo(0, this.$refs.scroll.scroll.maxScrollY, 500);
      }, 1000); // 渲染需要一定时间
      // 处理vue组件里的order数据和sessionStorage中的order数据
      /* const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
      tempOrder.add.push(param);
      this.order = tempOrder; */
      this.order.add.push(param);
      window.localStorage.setItem("order", window.JSON.stringify(this.order));
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    cancelClickOfAddOne() {
      this.isShowAddOneDialog = false;
      this.isShowCoverPage = false;
    },
    emptyCategoryNameToast() {
      this.$toast.error("请先填写类别名", 240);
    },
    hadSameTitleFromListToast() {
      this.$toast.error("该类别名已存在", 240);
    },
    imageUploadSuccessToast() {
      this.$toast.success("上传成功", 240);
    },
    async newImageUpload(param) {
      // console.log(param);
      const { className, index, formData } = param;
      try {
        const response = await $uploadStreamMediaFile(formData);
        if (response.Code === 200 && response.msg === "success") {
          this.$toast.success("上传成功", 240);
          const { details } = response;
          // eslint-disable-next-line camelcase
          const { image_url } = details;
          //const tempList = window.JSON.parse(window.JSON.stringify(this.list));
          if (className === "imageFileInput") {
            this.$set(this.list[index], "image", image_url);
            //tempList[index].image = image_url;
          }
          if (className === "thumbFileInput") {
            this.$set(this.list[index], "thumbnail", image_url);
            //tempList[index].thumbnail = image_url;
          }
          //this.list = tempList;
          window.localStorage.setItem("list", window.JSON.stringify(this.list));
          this.activeEditID = this.list[index].id;

          //const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
          const findInAdd = this.order.add.find(
            (item) => item.id === this.activeEditID
          );
          if (findInAdd) {
            if (className === "imageFileInput") {
              findInAdd.image = image_url;
            }
            if (className === "thumbFileInput") {
              findInAdd.thumbnail = image_url;
            }
          } else {
            const findInUpdate = this.order.update.find(
              (item) => item.id === this.activeEditID
            );
            if (findInUpdate) {
              if (className === "imageFileInput") {
                findInUpdate.image = image_url;
              }
              if (className === "thumbFileInput") {
                findInUpdate.thumbnail = image_url;
              }
            } else {
              if (className === "imageFileInput") {
                const obj = {
                  id: this.activeEditID,
                  image: image_url,
                };
                this.order.update.push(obj);
              }
              if (className === "thumbFileInput") {
                const obj = {
                  id: this.activeEditID,
                  thumbnail: image_url,
                };
                this.order.update.push(obj);
              }
            }
          }
          //this.order = tempOrder;
          window.localStorage.setItem(
            "order",
            window.JSON.stringify(this.order)
          );
          this.activeEditID = -1;
        } else {
          this.$toast.error("上传失败", 240);
        }
      } catch (err) {
        this.$toast.error("上传失败", 240);
      }
    },
    emptyNewTitleToast() {
      this.$toast.warn("类别名不能为空", 240);
    },
    findSameTitleToast() {
      this.$toast.warn("该类别名已存在", 240);
    },
    updateTitle(param) {
      const { index, newText } = param;
      this.activeEditID = this.list[index].id;
      this.$set(this.list[index], "name", newText); // 好像可以直接进行修改，而删除、添加和查找元素是不能直接进行的
      /* const tempList = window.JSON.parse(window.JSON.stringify(this.list));
      tempList[index].name = newText;
      this.list = tempList; */
      window.localStorage.setItem("list", window.JSON.stringify(this.list));

      // 处理工单数据
      // const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
      const findInAdd = this.order.add.find(
        (item) => item.id === this.activeEditID
      );
      if (findInAdd) {
        this.$toast.success("修改成功", 240);
        findInAdd.name = newText;
      } else {
        const findInUpdate = this.order.update.find(
          (item) => item.id === this.activeEditID
        );
        if (findInUpdate) {
          if (findInUpdate.name) {
            const original22 = this.originList.find(
              (item) => item.id === this.activeEditID
            );
            if (original22.name === newText) {
              this.$toast.warn("本次修改后与原类别名称一致", 240, 230);
              if (Object.keys(findInUpdate).length === 2) {
                const index33 = this.order.update.indexOf(findInUpdate);
                // console.log(index33);
                this.order.update.splice(index33, 1);
              } else {
                delete findInUpdate.name;
              }
            } else {
              this.$toast.success("修改成功", 240);
              findInUpdate.name = newText;
            }
          } else {
            this.$toast.success("修改成功", 240);
            findInUpdate.name = newText;
          }
        } else {
          this.$toast.success("修改成功", 240);
          const obj = {
            id: this.activeEditID,
            name: newText,
          };
          this.order.update.push(obj);
        }
      }
      //this.order = tempOrder;
      window.localStorage.setItem("order", window.JSON.stringify(this.order));
      this.activeEditID = -1;
    },
    titleNoChange() {
      this.$toast.warn("未检查到更改", 240);
    },
    showEditReasonDialog() {
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    async deployOrder() {
      console.log(this.order.add);
      const { message } = this.$refs.editReasonDialog;
      if (!message) {
        this.$toast.warn("请填写编辑理由", 240);
        return;
      }
      //const orderObj = window.JSON.parse(window.JSON.stringify(this.order));
      if (
        this.order.add.length === 0 &&
        this.order.update.length === 0 &&
        this.order.delete.length === 0
      ) {
        this.$toast.warn("未有有效编辑", 240);
        return;
      }
      if (this.order.add.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1200,
          work_operation: 4200,
          // content: window.JSON.parse(window.JSON.stringify(this.order.add)),
          content: {
            name: this.order.add[0].name,
            image: this.order.add[0].image,
            thumbnail: this.order.add[0].thumbnail,
          },
        };
      }
      if (this.order.update.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1200,
          work_operation: 4300,
          // content: window.JSON.parse(window.JSON.stringify(this.order.update)),
          content: {
            id: this.order.update[0].id,
            name: this.order.update[0].name,
            image: this.order.update[0].image,
            thumbnail: this.order.update[0].thumbnail,
          },
        };
      }
      if (this.order.delete.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1200,
          work_operation: 4900,
          content: {
            id: this.order.delete[0].id,
          },
        };
      }
      const response = await $deployOrder(requestPayload);
      this.isShowEditReasonDialog = false;
      //console.log(response)
      if (response) {
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("提交成功，请等待审核", 240, 200, 1000);
          // this.isShowEditReasonDialog = false;
          this.isShowCoverPage = false;
          this.order = {
            add: [],
            update: [],
            delete: [],
          };
          window.localStorage.setItem(
            "order",
            window.JSON.stringify(this.order)
          );

          // this.list = window.JSON.parse(window.JSON.stringify(this.originList));
          // window.localStorage.setItem("list", window.JSON.stringify(this.list));
          // setTimeout(() => {
          //   this.$toast.success("页面回退到修改前", 240);
          // }, 1000);
          this.$refs.scroll.scrollTo(0, 0, 300);
        } else {
          this.$toast.error("提交出错，请稍后再试", 240);
        }
      } else {
        alert("提交失败");
      }
    },
    cancelClickOfEditReasonDialog() {
      this.isShowEditReasonDialog = false;
      this.isShowCoverPage = false;
    },
    resetListAndOrder() {
      if (
        this.order.add.length === 0 &&
        this.order.update.length === 0 &&
        this.order.delete.length === 0
      ) {
        this.$toast.warn("当前没有修改", 240);
        return;
      }
      this.order.add.splice(0, this.order.add.length);
      this.order.update.splice(0, this.order.update.length);
      this.order.delete.splice(0, this.order.delete.length);
      window.localStorage.setItem("order", window.JSON.stringify(this.order));

      this.list.splice(0, this.list.length);
      const arr = window.JSON.parse(window.JSON.stringify(this.originList));
      this.list.push(...arr);
      window.localStorage.setItem("list", window.JSON.stringify(this.list));
      setTimeout(() => {
        this.$toast.success("页面回退到修改前", 240);
      }, 100);
    },
    scrollToTop() {
      this.$refs.scroll.scrollTo(0, 0, 500);
    },
    printPosition(position) {
      this.ifShowBackTop = -position.y > 400;
    },
    editDialogshow(param) {
      // console.log(param);
      this.editNum = param;
      this.editIndex = this.list[param];
      console.log(this.list[param]);
      this.isShowEditOneDialog = true;
      this.isShowCoverPage = true;
    },
    cancelClickOfEditOne() {
      this.isShowEditOneDialog = false;
      this.isShowCoverPage = false;
    },
    editOne(param) {
      // console.log(this.editNum,'num');
      console.log(param, "afteredit");
      this.isShowEditOneDialog = false;
      this.isShowCoverPage = false;
      this.order.update.push(param);
      // console.log(this.order.update);
      window.localStorage.setItem("order", window.JSON.stringify(this.order));
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
  },
  computed: {
    getAccount() {
      return window.localStorage.getItem("name");
    },
  },
  watch: {
    // showimg(val){
    //   if(val === false){
    //     this.showimg=true
    //   }
    // },
  },
  components: {
    Scroll,
    ContentList,
    AddOne,
    EditOne,
    EditReasonDialog,
    BackTop,
    Loading,
  },
};
</script>

<style scoped>
