  display: flex;
  height: 40px;
  align-items: center;
}
#category .buttonZone .addCategoryBtn,
#category .buttonZone .submitBtn,
#category .buttonZone .giveUpModificationBtn {
  font-family: siyuan;
  font-size: 22px;
  color: #000;
  font-weight: bold;

  cursor: pointer;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
}
#category .buttonZone .simulatedLine {
  height: 40px;
  width: 0px;
  border-right: 1px solid #000;
  margin-right: 18px;
  margin-left: 18px;
}
#category .buttonZone .addCategoryBtn:hover,
#category .buttonZone .submitBtn:hover,
#category .buttonZone .giveUpModificationBtn:hover {
  transform: translateZ(0) scale(1.11);
  -webkit-transform: translateZ(0) scale(1.09);
  -moz-transform: translateZ(0) scale(1.09);
  -o-transform: translateZ(0) scale(1.09);
}
.scroll-content {
  position: absolute;
  top: 180px;
  bottom: 40px;
  left: 380px;
  right: 130px;
  overflow: hidden;

  padding-right: 10px;

  border-radius: 25px 15px 15px 25px;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.super-enter-active,
.super-leave-active {
  transition: all 0.4s ease;
}
.super-enter-to,
.super-leave {
  opacity: 1;
}
.super-enter,
.super-leave-to {
  opacity: 0;
}
.right-content-cover-absolute {
  position: absolute;
  left: 240px;
  right: 0;
  top: 0;
  bottom: 0;

  display: flex;
  align-items: center;
  justify-content: center;

