               title="单击上传大图"> -->

          <!-- <img v-lazy="item.image"
               alt="big image of category"
               @load="imageLoad"
               @click="imageClick(index)"
               title="单击上传大图"> -->
          <img
            v-lazy="item.image"
            importance="auto"
            alt="big image of category"
            @load="imageLoad"
            @click="imageClick(index)"
            title="单击上传大图"
          />
        </div>
        <a-popconfirm
          title="确认删除？"
          ok-text="确认"
          cancel-text="取消"
          @confirm="deleteIconClick($event, index)"
        >
          <div class="error">
            <img
              src="../../../../../assets/image/providerTools/categoryManagement/error.svg"
            />
          </div>
        </a-popconfirm>

        <input
          type="file"
          v-show="false"
          class="imageFileInput"
          :data-image-id="index"
          @change="onFileChange($event, index)"
        />
      </div>

      <div class="thumbTitle">
        <div class="thumb">
          <div class="relativeLayout">
            <!-- <img v-lazy="'https://www.camptogo.com'+VMUrlDecode(item.thumbnail)"
                alt=""
                @load="imageLoad"
                @click="thumbClick(index)"
                :key="item.thumbnail"
                title="单击上传缩略图"> -->

            <img
              v-lazy="item.thumbnail"
              importance="auto"
              @load="imageLoad"
              @click="imageClick(index)"
            />
            <input
              type="file"
              v-show="false"
              class="thumbFileInput"
              :data-thumb-id="index"
              @change="onFileChange($event, index)"
            />
          </div>
        </div>

        <a-tooltip
          :getPopupContainer="
            (triggerNode) => {
              return triggerNode.parentNode;
            }
          "
        >
          <!-- <template #title>双击进行编辑，以回车键结束</template> -->
          <!-- <div class="title"
            :key="item.name"
            @dblclick="onClickTitle($event, index)"
            >{{item.name}}</div> -->
          <div class="title" :key="item.name" @click="imageClick(index)">
            {{ item.name }}
          </div>
        </a-tooltip>
      </div>
    </content-list-item>
  </div>
