<script>
import $ from "jquery";
import ContentListItem from "./ContentListItem.vue";
import { UrlDecode } from "../../../../../common/utils";

export default {
  name: "e-ContentList",
  data() {
    return {
      isShowBigImageEditButton: false,
    };
  },
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    VMUrlDecode(str) {
      return UrlDecode(str);
    },
    imageLoad() {
      this.$bus.$emit("itemImageLoad");
    },
    deleteIconClick(e, index) {
      this.$emit("deleteCategoryClick", index);
    },
    imageClick(index) {
      // const strIndex = `'${index.toString()}'`;
      // const fileInput = document.querySelector(`input[data-image-id=${strIndex}]`);
      // fileInput.click();
      // console.log(index);
      this.$emit("editDialogshow", index);
    },
    thumbClick(index) {
      const strIndex = `'${index.toString()}'`;
      const fileInput = document.querySelector(
        `input[data-thumb-id=${strIndex}]`
      );
      fileInput.click();
    },
    async onFileChange(e, index) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      if (!/image\/\w+/.test(files[0].type)) {
        // eslint-disable-next-line no-alert
        this.$toast.error("文件必须为图片！", 240);
        const fileInput = document.getElementById(inputId);
        fileInput.value = "";
        return;
      }
      const className = e.target.getAttribute("class");
      const formData = new FormData();
      formData.append("image", files[0]);
      const param = {
        className,
        index,
        formData,
      };
      this.$emit("newImageUpload", param);
    },
    onClickTitle(e, index) {
      const vm = this;
      const item = $(e.target);
      const oldText = item.text();
      const width = item.width() * 1;
      const height = item.height() * 0.9;
      // eslint-disable-next-line prefer-template
      const input = $(
        "<input type='text' style='width:" +
          width +
          "px;height:" +
          height +
          "px;background-color:var(--color-background-grey);border:1px solid var(--color-background-light-green);border-radius:30px;outline:none;'/>"
      );
      item.html(input);
      input.trigger("focus");
      input.val(oldText); // 获得焦点后设置input元素的value值，使input获得焦点时光标在字符串之后
      input.blur(function () {
        // console.log(oldText);
        const newText = $(this).val();
        if (newText === "") {
          item.html(oldText);
          vm.$emit("emptyNewTitle");
        } else if (newText === oldText) {
          item.html(oldText);
          vm.$emit("titleNoChange");
        } else {
          const findInList = vm.list.find((i) => i.name === newText);
          if (findInList) {
            vm.$emit("findSameTitle");
            item.html(oldText);
            return;
          }
          item.html(newText);
          const param = { index, newText };
          vm.$emit("updateTitle", param);
        }
      });
      input.keydown((event) => {
        if (event.keyCode === 13) {
          input.blur();
        }
      });
    },
  },
  components: {
    ContentListItem,
  },
};
</script>

